/* General */
body {
  margin: 0;
  font-family: "Arial", sans-serif;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  background-color: #f5f5f5;
}

.logo img {
  height: 40px;
  width: auto;
}

.auth-buttons {
  display: flex;
  gap: 15px;
}

.btn {
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 25px;
  transition: 0.3s;
  cursor: pointer;
}

.sign-in-btn,
.sign-up-btn {
  border: 2px solid #555;
  color: #555;
  background-color: transparent;
}

.sign-in-btn:hover,
.sign-up-btn:hover {
  background-color: #555;
  color: white;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  background-color: #fff;
  width: 350px;
  padding: 20px;
  border-radius: 10px;
  position: relative;
  animation: fade-in 0.5s ease-out;
  text-align: center;
}

.modal h2 {
  margin-bottom: 20px;
}

.input-field {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  padding-right: 0;
  border: 2px solid #555;
  border-radius: 8px;
  font-size: 16px;
}

.submit-btn {
  width: 100%;
  background-color: #00bcd4;
  color: #fff;
  border: none;
  padding: 10px 0;
  font-size: 18px;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.3s;
}

.submit-btn:hover {
  background-color: #008c9e;
}

.switch-text {
  margin-top: 10px;
}

.switch-btn {
  color: #00bcd4;
  border: none;
  background: none;
  cursor: pointer;
  text-decoration: underline;
}

.switch-btn:hover {
  color: #008c9e;
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: #e53935;
  color: #fff;
  border: none;
  border-radius: 100%;
  width: 10px;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  cursor: pointer;
}

.close-btn:hover {
  background-color: #c62828;
}

@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Event Cards Section */
.event-cards-section {
  padding: 20px;
  text-align: center;
}

.event-cards {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.event-card {
  width: 300px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: 0.3s;
}

.event-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.event-card:hover {
  transform: translateY(-5px);
}

.event-info {
  padding: 15px;
  text-align: left;
}

.event-info h3 {
  font-size: 20px;
  margin: 10px 0;
}

.event-info p {
  font-size: 14px;
  color: #555;
}

.event-info .event-date {
  font-size: 12px;
  font-weight: bold;
  color: #888;
}

.event-info .event-price {
  font-size: 16px;
  font-weight: bold;
