/* Puzzle shape adjustments */
.puzzle-piece.top-left::before,
.puzzle-piece.top-right::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: #f5faff;
  border: 2px solid #0056b3;
  border-radius: 50%;
}

.puzzle-piece.top-left::before {
  top: -12px;
  left: 50%;
  transform: translateX(-50%);
}

.puzzle-piece.top-right::before {
  top: -12px;
  right: 50%;
  transform: translateX(50%);
}

