/* Mission and Vision Containers */
.mission-container,
.vision-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  gap: 20px;
}

/* Mission & Vision Boxes */
.mission-box,
.vision-box {
  background-color: #f5faff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: fit-content;
}

.mission-box h3,
.vision-box h3 {
  color: #0056b3;
  font-size: 1.8rem;
  margin-bottom: 15px;
}

.mission-box p,
.vision-box p {
  font-size: 1rem;
  color: #333;
  line-height: 1.6;
}

/* Mission & Vision Images */
.mission-image img,
.vision-image img {
  width: 100%;
  max-width: 400px; /* Restrict image size */
  height: auto;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Responsive Design */
@media (max-width: 768px) {
  .mission-container,
  .vision-container {
    grid-template-columns: 1fr;
  }

  .mission-image img,
  .vision-image img {
    max-width: 100%; /* Allow full width for smaller screens */
  }
}

/* Hover Effects on Boxes */
.mission-box:hover,
.vision-box:hover {
  transform: scale(1.05);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.2);
}

/* Hover Effects on Images */
.mission-image img:hover,
.vision-image img:hover {
  transform: scale(1.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
}

/* Fade-In Animation for Boxes and Images */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Apply Animation to Mission and Vision Boxes */
.mission-box,
.vision-box {
  animation: fadeIn 0.8s ease forwards;
  opacity: 0; /* Initial state for animation */
}

/* Apply Animation to Mission and Vision Images */
.mission-image img,
.vision-image img {
  animation: fadeIn 1s ease forwards;
  animation-delay: 0.3s; /* Add delay for a staggered effect */
  opacity: 0; /* Initial state for animation */
}

/* Hover Effects for Boxes */
.mission-box:hover,
.vision-box:hover {
  transform: scale(1.05);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
  background-color: #f0f8ff; /* Slightly lighter background on hover */
}

/* Hover Effects for Images */
.mission-image img:hover,
.vision-image img:hover {
  transform: scale(1.1) rotate(2deg); /* Adds slight rotation for a dynamic look */
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4);
}

/* Add Hover Effects to the Text Inside Boxes */
.mission-box:hover .text,
.vision-box:hover .text {
  color: #007bff; /* Change text color on hover */
  transition: color 0.3s ease;
}

/* Hover Effects for Entire Section */
.mission-section:hover,
.vision-section:hover {
  background-color: #e6f7ff; /* Light background for entire section */
  transition: background-color 0.3s ease;
}

